@import url("https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
  font-family: "Ubuntu", sans-serif;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
}

ul,
li {
  list-style: none;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

button {
  cursor: pointer;
}

.container {
  width: 90.2%;
  margin: auto;
}
@media screen and (max-width: 992px) {
  .container {
    width: 95%;
  }
}

.row {
  display: flex;
}

.grid-cards {
  display: grid;
}

[id] {
  scroll-margin-top: 9.8rem;
}

#content {
  visibility: hidden;
}

#loader {
  position: fixed;
  inset: 0;
  background: #f7fff6;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  transition: opacity 0.6s ease; /* fade speed */
}

#loader.fade-out {
  opacity: 0;
}

.spinner {
  width: 60px;
  height: 60px;
  border: 6px solid #029837;
  border-top: 6px solid #333;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
.secondarybg {
  background-color: #f7fff6;
}

.whitebg {
  background-color: #fff;
}

header {
  position: sticky;
  top: 0;
  z-index: 1000;
  height: 9.8rem;
  background: rgba(255, 255, 255, 0.3);
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}
header .container .row .navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 0;
}
header .container .row .navbar .logo {
  width: 7rem;
}
header .container .row .navbar .logo img {
  width: 100%;
  height: 100%;
}
header .container .row .navbar .burger-menu {
  display: none;
  width: 30px;
  cursor: pointer;
}
header .container .row .navbar .burger-menu span {
  display: block;
  height: 3px;
  margin: 6px 0;
  background: #000;
  border-radius: 2px;
  transition: 0.3s;
}
header .container .row .navbar .navlist {
  display: flex;
  align-items: center;
  gap: 4.1rem;
}
header .container .row .navbar .navlist .navelement .navlink {
  position: relative;
  display: inline-block;
  font-size: 1.4rem;
  line-height: 24px;
  font-weight: 400;
  color: #000033;
}
header .container .row .navbar .navlist .navelement .navlink:not(.cta)::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  height: 2.5px;
  border-radius: 2px;
  background-color: black;
  width: 100%;
  transform: scaleX(0);
  transform-origin: center;
  transition: transform 0.5s ease;
}
header .container .row .navbar .navlist .navelement .navlink:hover::after {
  transform: scaleX(1);
}
header .container .row .navbar .navlist .navelement .navlink.current::after {
  position: absolute;
  bottom: -2px;
  left: 0;
  height: 2.5px;
  border-radius: 2px;
  background-color: black;
  width: 100%;
  transform: scaleX(1);
}
header .container .row .navbar .navlist .navelement .cta.navlink {
  color: #029837;
  border: 1px solid #029837;
  padding: 13.68px 39.5px;
  background-color: transparent;
  font-size: 1.6rem;
  letter-spacing: -1%;
  line-height: 24px;
  border-radius: 2rem;
  font-weight: 400;
  transition: all 0.5s ease;
}
header .container .row .navbar .navlist .navelement .cta.navlink:hover {
  background-color: #029837;
  color: #ffffff;
  border: 1px solid #029837;
}
@media screen and (max-width: 992px) {
  header .container .row .navbar .navlist .navelement .cta.navlink {
    border: 1px solid #029837;
    padding: 6px 15px;
    font-size: 1.2rem;
    line-height: normal;
  }
}
header .container .row .navbar .navlist .navelement .cta.navlink.act {
  color: #ffffff;
  background-color: #029837;
  padding: 13.68px 41px;
  font-size: 1.6rem;
  line-height: 26px;
  font-weight: 400;
  border-radius: 2rem;
  transition: all 0.5s ease;
  border: 1px solid transparent;
}
header .container .row .navbar .navlist .navelement .cta.navlink.act:hover {
  background-color: transparent;
  color: #029837;
  border: 1px solid #029837;
  scale: 1.05;
}
@media screen and (max-width: 992px) {
  header .container .row .navbar .navlist .navelement .cta.navlink.act {
    padding: 10px 30px;
    font-size: 1.5rem;
    line-height: normal;
  }
}

