@media screen and (max-width: 992px) {
  .prices .container .row {
    padding: 7rem 0;
    gap: 6rem;
  }
  .prices .container .row .sectiontitle {
    width: 100%;
  }
  .prices .container .row .grid-cards {
    grid-template-columns: repeat(1, 1fr);
    column-gap: 0;
    row-gap: 3rem;
  }
  .prices .container .row .grid-cards .pricecard {
    padding: 3.4rem;
    gap: 2.6rem;
  }
  .prices .container .row .grid-cards .price .value,
  .prices .container .row .grid-cards .price .currency {
